import { useState } from "react";
import imgs from "../imgsList";
function MobilePage() {
  const [counter,setCounter] = useState(0);
  return (
    <section className="mobile-responsive">
      <div className="everything-in">
        <div className="movement">
          <button
            name="move-back"
            className="move-picture"
            onClick={() => {
              counter === 0
                ? setCounter(imgs.length - 1)
                : setCounter(counter - 1);
            }}
          >
            <svg
              width="12"
              height="18"
              id="move-back"
              onClick={() => {
                counter === 0
                  ? setCounter(imgs.length - 1)
                  : setCounter(counter - 1);
              }}
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                id="move-back"
                d="M11  3 3 9l8 8"
                stroke="#1D2026"
                strokeWidth="3"
                fill="none"
                fillRule="evenodd"
              />
            </svg>
          </button>
        </div>
        <div>
          <img src={imgs[counter].product} alt="" />
        </div>
        <div className="movement">
            <button
              name="move-forward"
              className="move-picture"
              onClick={() => {
                counter > imgs.length - 2
                  ? setCounter(0)
                  : setCounter(counter + 1);
              }}
            >
              <svg
                width="13"
                height="18"
                xmlns="http://www.w3.org/2000/svg"
                id="move-forward"
                onClick={() => {
                  counter > imgs.length - 2
                    ? setCounter(0)
                    : setCounter(counter + 1);
                }}
              >
                <path
                  id="move-forward"
                  d="m2 2 8 8-8 8"
                  stroke="#1D2026"
                  strokeWidth="3"
                  fill="none"
                  fillRule="evenodd"
                />
              </svg>
            </button>
        </div>
      </div>
    </section>
  );
}

export default MobilePage;