import {BrowserRouter as Router, Routes, Route} from 'react-router-dom'
import Collaction from "./NavBarElems/collections";
import Contact from "./NavBarElems/contact";
import Men from "./NavBarElems/men";
import Women from "./NavBarElems/women";
import About from "./NavBarElems/about";
import NavBar from './NavBarElems/Nav';
import {useState} from "react"

function Menu(props) {
    const [userBuy,setUserBuy] = useState(0);
    const percent = 50;
    const priceBefore = 250; 
    const priceNow = priceBefore*0.5;
    return (  
        <main>
            <Router>
                <NavBar percent={percent} priceBefore={priceBefore} priceNow={priceNow} userBuy={userBuy} setUserBuy={setUserBuy}/>
                    <Routes>
                        <Route path="/" element={<Collaction changeScreenView={props.changeScreenView} percent={percent} priceBefore={priceBefore} priceNow={priceNow} userBuy={userBuy} setUserBuy={setUserBuy}/>} />
                        <Route path="/collections" element={<Collaction changeScreenView={props.changeScreenView} percent={percent} priceBefore={priceBefore} priceNow={priceNow} userBuy={userBuy} setUserBuy={setUserBuy}/>} />
                        <Route path="/men" element={<Men />} />
                        <Route path="/women" element={<Women />} />
                        <Route path="/about" element={<About />} />
                        <Route path="/contact" element={<Contact />} />
                    </Routes>
            </Router>
        </main>
    );
}

export default Menu;