import "./collection-page.css";
import imgs from "../imgsList";
import { AiOutlineShoppingCart } from "react-icons/ai";
import { useState } from "react";
import MobilePage from "../responsive/MobileMain";
function Collection(props) {
  const [counter, setCounter] = useState(0);
  const mainImgStyle = {
    border: "1px solid saddlebrown",
    opacity: "0.5",
  };
  const images = imgs.map((l, index) => {
    
    if (index === counter) {
      return (
        <img
          style={mainImgStyle}
          src={l.product}
          key={index}
          id={index}
          onClick={(e) => setCounter(parseInt(e.target.id))}
        />
      );
    } else {
      return (
        <img
          src={l.product}
          id={index}
          key={index}
          onClick={(e) => setCounter(parseInt(e.target.id))}
        />
      );
    }
  });
  const buyProduct = (e) => {
    const name = e.target.name;
    if (name === "plus") {
      props.setUserBuy(props.userBuy + 1);
    } else if (name === "minus") {
      if (props.userBuy > 0) {
        props.setUserBuy(props.userBuy - 1);
      }
    }
  };
  return (
    <main className="main-page">
      <MobilePage/>
      <section className="images-pleace">
        <div className="main-img">
          <img src={imgs[counter].product} onClick={props.changeScreenView} alt="product image"/>
        </div>
        <div className="poducts-imgs">{images}</div>
      </section>
      <section className="text-and-price">
        <div className="show-users-about">
          <p className="company-name">SNEAKER COMPANY</p>
          <h1 className="company-ad">
            <b>Fall Limited Edition Sneakers</b>
          </h1>
          <div className="offer-to-client">
            These low-profile sneakers are your perfect casual wear companion.
            Featuring a durable rubber outer sole. they'll withstand everything
            the weather can offer
          </div>
          <div className="all-about-price">
            <div className="price-and-percent">
              <p><i id="usd-icon" className="fas fa-dollar-sign"></i>125.00</p>
              <div>50%</div>
            </div>
            <div className="previes-price"><i id="usd-icon" className="fas fa-dollar-sign"></i> {props.priceBefore}.00</div>
          </div>
          <div className="buy-product">
            <div className="btn-quantity">
              <button className="minus" name="minus" onClick={buyProduct}>
                -
              </button>
              <div className="quantity">{props.userBuy}</div>
              <button className="plus" name="plus" onClick={buyProduct}>
                +
              </button>
            </div>
            <button className="add-to-cart">
              <h3>
                <AiOutlineShoppingCart />
              </h3>
              <p>Add to cart</p>
            </button>
          </div>
        </div>
      </section>
    </main>
  );
}

export default Collection;
